#box-container{
    max-width: 900px;
    margin: 50px auto 0;
}

#box-container header {
    display: flex;
    justify-content: center;
    align-items: center;
}

#box-container header h1 {
    font-size: 21px;
    height: 100px;
    padding-left:15px;
    margin-left:15px;
    border-left: 1px solid #ddd;
    padding-top: 60px;
}

#box-container header button svg {
    margin-right: 10px;
}

#box-container header button:hover {
    opacity: 0.9;
}

#box-container .upload{
    border-radius:4px;
    padding: 30px;
    text-align: center;
    border:1px dashed #ddd;
    color:#999;;
    margin-top:50px;
    cursor: pointer;
}

#box-container ul {
    margin:30px 0;
    list-style: none;
}


#box-container ul li {
    display:flex;
    justify-content: space-between;
    align-items: center;
}

#box-container ul li + li{
    padding-top:20px;
    margin-top:20px;
    border-top:1px solid #eee;
}

#box-container ul li .fileInfo {
    display:flex;
    align-items: center;
    text-decoration: none;
}

#box-container ul li .fileInfo strong{
    font-weight: normal;
    font-display: 14px;
    margin-left: 10px;
    color:#333;
}

#box-container ul li span {
    color: #999;
    font-size: 13px;
}
